import React, { useEffect, useState } from 'react';
import logo from './logo.svg';
import ResponsiveGridLayout from 'react-grid-layout'
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs'
import './App.css';
function App() {
  const [layout, setLayout] = useState<any>(null);
  const [selectedPage, setSelectedPage] = useState<any>(null);
  const [selectedPageI, setSelectedPageI] = useState<number>(0);
  const [pageW, setPageW] = useState<number>(0);

  useEffect(() => {
    const fake = {
      layout: {
        uid: "RORDv95clxqx",
        name: "Example Smartphone",
        pages: [
          {
            uid: "CH5Wa0MwuF1Y",
            name: "Page 1",
            items: [
              { name: "A", grid: { i: "kGSYfDAdp82X", x: 0, y: 0, w: 3, h: 3 }, color: "#2b6699", bgcolor: "lightgray", bgimg: "none", bgsize: "cover", bgpos: { x: "center", y: "center" }, bgrepeat: "no-repeat", border: "none", borderRadius: "none", action: "ma3HOutCTvGd" },
              { name: "B", grid: { i: "UMjpcIy5GEhC", x: 3, y: 0, w: 3, h: 3 }, color: "#2b6699", bgcolor: "lightgray", bgimg: "none", bgsize: "cover", bgpos: { x: "center", y: "center" }, bgrepeat: "no-repeat", border: "none", borderRadius: "none", action: "none" },
              { name: "C", grid: { i: "EeQ2zSUp8jRu", x: 6, y: 0, w: 3, h: 3 }, color: "#2b6699", bgcolor: "lightgray", bgimg: "none", bgsize: "cover", bgpos: { x: "center", y: "center" }, bgrepeat: "no-repeat", border: "none", borderRadius: "none", action: "none" },
              { name: "D", grid: { i: "8q9CsVKVQWgg", x: 9, y: 0, w: 3, h: 3 }, color: "#2b6699", bgcolor: "lightgray", bgimg: "none", bgsize: "cover", bgpos: { x: "center", y: "center" }, bgrepeat: "no-repeat", border: "none", borderRadius: "none", action: "none" }
            ],
            standalone: false,
            pageConfig: { bgcolor: "#435e7a", bgimg: "none", bgsize: "cover", bgpos: { x: "center", y: "center" }, bgrepeat: "no-repeat" },
            pageListConfig: { bgcolor: "transparent", bgimg: "none", bgsize: "cover", bgpos: { x: "center", y: "center" }, bgrepeat: "no-repeat", margin: "0", padding: "5px", justifyitems: "space-between" },
            pageItemConfig: { bgcolor: "blue", bgimg: "none", bgsize: "cover", bgpos: { x: "center", y: "center" }, bgrepeat: "no-repeat", border: "none", borderRadius: "none", margin: "0", padding: "0", width: "120px", height: "50px" }
          },
          {
            uid: "SH5Wa0MwuF1Y",
            name: "Page 2",
            items: [
              { name: "New Item", grid: { i: "WgyYyGB4w51f", x: 0, y: 3, w: 2, h: 2 }, color: "#2b6699", bgcolor: "lightgray", bgimg: "none", bgsize: "cover", bgpos: { x: "center", y: "center" }, bgrepeat: "no-repeat", border: "none", borderRadius: "none", action: "none" },
              { name: "New Item", grid: { i: "AKcEC2MeywOM", x: 0, y: 5, w: 7, h: 2 }, color: "#2b6699", bgcolor: "lightgray", bgimg: "none", bgsize: "cover", bgpos: { x: "center", y: "center" }, bgrepeat: "no-repeat", border: "none", borderRadius: "none", action: "none" }
            ],
            standalone: false,
            pageConfig: { bgcolor: "#435e7a", bgimg: "none", bgsize: "cover", bgpos: { x: "center", y: "center" }, bgrepeat: "no-repeat" },
            pageListConfig: { bgcolor: "transparent", bgimg: "none", bgsize: "cover", bgpos: { x: "center", y: "center" }, bgrepeat: "no-repeat", margin: "0", padding: "5px", justifyitems: "space-between" },
            pageItemConfig: { bgcolor: "blue", bgimg: "none", bgsize: "cover", bgpos: { x: "center", y: "center" }, bgrepeat: "no-repeat", border: "none", borderRadius: "none", margin: "0", padding: "0", width: "120px", height: "50px" }
          }
        ]
      }
    };
    //setLayout(fake.layout)

         const fetchLayouts = async () => {
      try {
        const response = await fetch('/layout');
        console.log(response);
        
        if (response.ok) {
          const data = await response.json();
          console.log(data.layout);
          setLayout(data.layout)
        } else {
          throw new Error('Erreur lors de la récupération des layouts');
        }
      } catch (error) {
        console.error(error);
      }
    };

    fetchLayouts() 
    
    window.addEventListener('resize', ()=>{
      setPageW(window.innerWidth)
    });

  }, []);

  useEffect(() => {
    if (layout) {
      handleChangePage(0)
    }

  }, [layout]);

  const handleChangePage = (index: number) => {
    setSelectedPage(layout.pages[index])
    setSelectedPageI(index)
    setPageW(window.innerWidth)
  }
  const handleSendKey = (e: any,uid: string) => {
    console.log(uid);
    e.preventDefault()
     fetch(`/key/${uid}`, {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json'
      }
    }) 
  }
  return (
    <div >
      {layout && selectedPage &&
        <div id="layoutdisplay" style={{
          backgroundRepeat: selectedPage.pageConfig.bgrepeat,
          backgroundPositionX: selectedPage.pageConfig.bgpos.x,
          backgroundPositionY: selectedPage.pageConfig.bgpos.y,
          backgroundSize: selectedPage.pageConfig.bgsize,
          backgroundColor: selectedPage.pageConfig.bgcolor,
          backgroundImage: `url("images/${selectedPage.pageConfig.bgimg}")`
        }}>
          <Tabs selectedIndex={selectedPageI} onSelect={(index) => handleChangePage(index)}>
            <TabList style={{
              backgroundRepeat: selectedPage.pageListConfig.bgrepeat,
              backgroundPositionX: selectedPage.pageListConfig.bgpos.x,
              backgroundPositionY: selectedPage.pageListConfig.bgpos.y,
              backgroundSize: selectedPage.pageListConfig.bgsize,
              backgroundColor: selectedPage.pageListConfig.bgcolor,
              justifyContent: selectedPage.pageListConfig.justifyitems,
              margin: selectedPage.pageListConfig.margin,
              padding: selectedPage.pageListConfig.padding,
              backgroundImage: `url("file:///../${selectedPage.pageListConfig.bgimg}")`
            }} >
              {layout.pages.map((page: any, index: number) => (
                <Tab
                  style={{
                    backgroundImage: `url("file:///../${page.pageItemConfig.bgimg}")`,
                    backgroundSize: page.pageItemConfig.bgsize,
                    backgroundColor: page.pageItemConfig.bgcolor,
                    backgroundRepeat: page.pageItemConfig.bgrepeat,
                    backgroundPositionX: page.pageItemConfig.bgpos.x,
                    backgroundPositionY: page.pageItemConfig.bgpos.y,
                    margin: page.pageItemConfig.margin,
                    padding: page.pageItemConfig.padding,
                    border: page.pageItemConfig.border,
                    borderRadius: page.pageItemConfig.borderRadius,
                    width: page.pageItemConfig.width
                  }}
                  key={page.uid}>{page.name}
                </Tab>
              ))}
            </TabList>
            {layout.pages.map((page: any, index: number) => (
              <TabPanel key={index}

              >
                <ResponsiveGridLayout
                autoSize={true}
                  compactType={null}
                  width={pageW}
                  cols={12}
                  rowHeight={30}
                  preventCollision={true}
                  isBounded={false}
                >
                  {page.items.map((item: any) => (
                    <button
                      className="btn"
                      onClick={(e) => handleSendKey(e,item.action)}
                      key={item.grid.i}
                      data-grid={{
                        x: item.grid.x,
                        y: item.grid.y,
                        w: item.grid.w,
                        h: item.grid.h,
                        static: true
                      }}
                      style={{
                        backgroundRepeat: item.bgrepeat,
                        backgroundPositionX: item.bgpos.x,
                        backgroundPositionY: item.bgpos.y,
                        backgroundSize: item.bgsize,
                        backgroundColor: item.bgcolor,
                        color: item.color,
                        backgroundImage: `url("file:///../${item.bgimg}")`,
                        borderRadius: item.borderRadius,
                        border: item.border
                      }}
                    >
                      {item.name}
                    </button>
                  ))}
                </ResponsiveGridLayout>
              </TabPanel>
            ))}
          </Tabs>
        </div>
      }
    </div>
  );
}

export default App;
